import React, { Component } from 'react';
import ServiceCard from './servicecard';

class Service extends Component {
  render() {
    return (
        <div>
            <ServiceCard 
              PrimaryText = 'First Service' 
              SecondaryText = 'Description of service' 
              ImagePath = '/here' 
              ImageTitle = 'First Image'
            /> <br />
            <ServiceCard 
              PrimaryText = 'Secondary Service' 
              SecondaryText = 'Description of service'
              ImagePath = '/here' 
              ImageTitle = 'First Image'
            /> <br />
            <ServiceCard 
              PrimaryText = 'Third Service' 
              SecondaryText = 'Description of service'
              ImagePath = '/here' 
              ImageTitle = 'First Image'
            /> <br />
        </div>
    );
  }
}

export default Service;



