import React from 'react';
import { TextField, Button } from '@material-ui/core';

const styles = theme => ({
  button: {
    margin: theme.spacing.unit,
  },
  input: {
    display: 'none',
  },
});

function Careers(props) {
  const classes = {props};
    return (
        <div>
            Current open job openings will be posted here. <br/> 
            Please feel free to send your resume, thanks! <br/>
            <a href="mailto:info@novelcath.com?subject=Resume%20Submitted%20">
                Submit
            </a>
        </div>
    );
}

export default Careers;



