import React, { Component } from 'react';
import AboutUsCard from './aboutuscard';

class AboutUs extends Component {
  render() {
    return (
        <div className = 'aboutus'>
            <AboutUsCard 
              Name = 'First One' 
              Details = 'CEO - etc'
              Image = '/path.jpg'
              ImageTitle = 'Cool Picture'
            /> <br/>
            <AboutUsCard 
              Name = 'Second One' 
              Details = 'CEO - etc'
              Image = '/path.jpg'
              ImageTitle = 'Cool Picture'
            /> <br/>
            <AboutUsCard 
              Name = 'Third One' 
              Details = 'CEO - etc'
              Image = '/path.jpg'
              ImageTitle = 'Cool Picture'
            /> <br/>
            <AboutUsCard 
              Name = 'Fourth One' 
              Details = 'CEO - etc'
              Image = '/path.jpg'
              ImageTitle = 'Cool Picture'
            /> <br/>
        </div>
    );
  }
}

export default AboutUs;



